<template>
  <div class="form-elements">
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.departments.create' | translate">
          <div class="row">
            <div class="col">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">
                    <div class="input-group">
                      <input
                        id="title"
                        name="title"
                        v-model="title"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="title">{{'department.title'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('title')"
                             class="help text-danger">{{
                        errors.first('title')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('slug')}">
                    <div class="input-group">
                      <input
                        id="slug"
                        name="slug"
                        v-model="slug"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('slug')"></i>
                      <label class="control-label" for="slug">{{'page.slug'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('slug')"
                             class="help text-danger">{{
                        errors.first('slug')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('sort')}">
                    <div class="input-group">
                      <input
                        id="sort"
                        type="number"
                        name="sort"
                        v-model="sort"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('sort')"></i>
                      <label class="control-label" for="sort">{{'department.sort'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('sort')"
                             class="help text-danger">{{
                        errors.first('sort')
                        }}
                      </small>
                    </div>
                  </div>
                </div> 
              </div>

              <div class="row mt-5">
                <div class="col">
                  <Editor @editor="handleEditor"/>
                </div>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col mt-5 text-center">
              <button class="btn btn-success"  @click="add">
                {{'buttons.submit' | translate}}
              </button>
            </div>
          </div>          
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>

<script>
  import api from 'src/packages/Api'
  import Editor from 'view/components/editor'

  export default {
    name: 'create-department',
    data () {
      return {
        title: '',
        slug: '',
        clinics: '',
        data: '',
        sort: '',

        editor: null
      }
    },
    components: {
      Editor,
    },
    methods: {
      handleEditor (editor) {
        this.editor = editor
      },

      add () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = {
              title: this.title,              
              clinics: this.clinics,
              data: this.editor.getContents(),
              sort: this.sort
            }

            api.post('departments', data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('department.created'), type: 'success' }
              )
              this.$router.push({ name: 'department', params: { id: response.data.id } })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

